.o-grid {
	@mixin flex-row wrap, stretch, space-between;

	&__item {
		flex: 0 0 100%;

		&--1of2 {
			flex: 0 0 50%;
		}

		&--1of4 {
			flex: 0 0 25%;
		}

		&--1of3 {
			flex: 0 0 calc(100% / 3);
		}

		&--2of3 {
			flex: 0 0 66.66667%;
		}

		&--1of5 {
			flex: 0 0 20%;
		}

		&--1of6 {
			flex: 0 0 calc(100% / 6);
		}
	}
}



